#magicContainer {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	height: 100dvh;
	z-index: 0;
	opacity: 0;
	transition: opacity 1s ease-in-out;
	pointer-events: none;
}

